#dialog {
    border: none;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#form {
    font-family: '3dumb';
    color: var(--title-color);
    font-weight: bold;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 3fr;
    gap: 1rem;
}

#form > input {
    height: 1.5rem;
}

.buttons {
    align-self: center;
    display: flex;
    gap: 20px;
}

input {
    border: 1px solid var(--title-color);
    border-radius: 5px;
    color: rgb(88, 88, 255);
}

input:focus,
#priority:focus,
#projects:focus,
#notes:focus {
    outline: none;
}

#priority,
#notes {
    border: 1px solid var(--title-color);
    color:rgb(88, 88, 255);
    border-radius: 5px;
}

.projects-list input{
    height: 1.5rem;
    width: 100%;
}

#cancel,
#submit {
    font-family: '3dumb';
    width: 100px;
    padding: 1px 5px;
    color: var(--title-color);
    font-weight: 1000;
    border: 1px solid var(--title-color);
    border-radius: 5px;
    background-color:transparent;
    cursor: pointer;
