___CSS_LOADER_EXPORT___.push([module.id, `.todo-items {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.item {
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.card {
    padding: 1rem 2rem;
    width: 70%;
    border: 1px solid #ff8c00;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 1rem;
    transition: 250ms linear;
}

.card > * {
    margin: 0px;
}

.card:hover {
    scale: 1.02;
}

.card-title {
    font-family: '3dumb';
    font-size: 1.5rem;
    font-weight: bold;
}

.card-duedate {
    font-style: italic;
    font-weight: bold;
    font-size: 1rem;
}

.card-project {
    font-family: '3dumb';
    font-weight: bold;
    font-size: 1.5rem;
}

.high-priority {
    background-color: rgb(231, 94, 151);
}

.medium-priority {
    background-color: #b07df3;
}

.low-priority {
    background-color: rgb(129, 216, 129);
}

.card-circle-unchecked,
.card-circle-checked {
    width: 30px;
    height: 30px;
}

.card-delete {
    width: 4%;
    height: 4%;
}

.card-delete:hover {
    cursor: pointer;
    scale: 1.1;
}

.card-delete:active {
    scale: 1;
}

.card-circle-checked.card-checked {
    display: inline;
}

.item.item-checked > .card {
    text-decoration: line-through;
    opacity: 0.5;
}`, "",{"version":3,"sources":["webpack://./src/css/todoItem.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,yBAAyB;IACzB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;IACnB,wBAAwB;AAC5B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,oBAAoB;IACpB,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,oCAAoC;AACxC;;AAEA;;IAEI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,eAAe;IACf,UAAU;AACd;;AAEA;IACI,QAAQ;AACZ;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB","sourcesContent":[".todo-items {\r\n    height: 100%;\r\n    overflow-y: auto;\r\n    overflow-x: hidden;\r\n}\r\n\r\n.item {\r\n    margin: 2rem;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 1rem;\r\n}\r\n\r\n.card {\r\n    padding: 1rem 2rem;\r\n    width: 70%;\r\n    border: 1px solid #ff8c00;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    border-radius: 1rem;\r\n    transition: 250ms linear;\r\n}\r\n\r\n.card > * {\r\n    margin: 0px;\r\n}\r\n\r\n.card:hover {\r\n    scale: 1.02;\r\n}\r\n\r\n.card-title {\r\n    font-family: '3dumb';\r\n    font-size: 1.5rem;\r\n    font-weight: bold;\r\n}\r\n\r\n.card-duedate {\r\n    font-style: italic;\r\n    font-weight: bold;\r\n    font-size: 1rem;\r\n}\r\n\r\n.card-project {\r\n    font-family: '3dumb';\r\n    font-weight: bold;\r\n    font-size: 1.5rem;\r\n}\r\n\r\n.high-priority {\r\n    background-color: rgb(231, 94, 151);\r\n}\r\n\r\n.medium-priority {\r\n    background-color: #b07df3;\r\n}\r\n\r\n.low-priority {\r\n    background-color: rgb(129, 216, 129);\r\n}\r\n\r\n.card-circle-unchecked,\r\n.card-circle-checked {\r\n    width: 30px;\r\n    height: 30px;\r\n}\r\n\r\n.card-delete {\r\n